.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
body{
  margin: 0;
  padding: 0;
  font-family: 'Arial';
  background-color: #e2e2e2;
  height: 100vh;
}
*{
  margin: 0;
  padding: 0;
}
#root {
  --main-color: #ff9121;
  --secundary-color: #fb821f;
  --text-color: #666;
}
a{
  text-decoration: none;
  color: inherit;
}
.admin {
  z-index: 1;
  position: relative;
  overflow: hidden;
  height: 100%;
  display: grid;
  grid-template-columns: 250px 1fr;
  height: 100vh;
}

.main-header{
  text-align: center;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background: #00a5ff;
  align-items: start;
  height: 100%;
  border-right: 3px solid white;
}
.main-header > div > p{
  font-size: 18px;
  color: white;
  width: 140%;
  font-weight: bold;
  border-bottom: 1px solid white;
  text-align: center;
  display: block;
  padding-bottom: 10px;
}
.main-header .head-item{
  display: grid;
  grid-template-columns: 80px 1fr;
  justify-items: center;
  align-items: center;

  width: 100%;
  vertical-align: bottom;
  cursor:pointer;
  z-index: 2;
  position: relative;
  margin-top: 15px;

  -webkit-transition: -webkit-transform .3s ease;

  transition: -webkit-transform .3s ease;

  transition: transform .3s ease;

  transition: transform .3s ease, -webkit-transform .3s ease;
}
.sub-menu:hover ul{
  height: 50px;
}
.sub-menu ul{
  grid-column: 2;
  list-style: none;
  justify-self: left;
  padding-left: 10px;
  height: 0;
  overflow: hidden;

  -webkit-transition: height .3s ease;

  transition: height .3s ease;
}
.sub-menu ul li{  color: white;
  font-size: 12px;
  margin: 0px 0 20px;

}
.main-header .logo{
  margin: 20px 20px 5px 95px;
  align-self: center;
  width: 65px;
}
.head-item:hover{
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}
.main-header .icon-env{
  background-color: transparent;
}
.icon-env{
  border-radius: 50%;
  background-color: var(--main-color);
  padding: 10px;
  width: 25px;
  height: 25px; 
  display: inline-block;
  grid-column: 1;
  grid-row: 1;
}

.main-header .head-item img{
  width: 25px;
  height: 25px;
  vertical-align: middle;
  overflow: visible;
}
.main-header .head-item p{
  font-size: 22px;
  color: white;
  padding: 4px 10px;
  z-index: 1;
  grid-column: 2;
  display: inline-block;
  width: 100%;
  text-align: left;
}
