/* modal */
#modal .add{
  position: relative;
  top: auto;
  top: initial;
  left: auto;
  left: initial;
  width: initial;
  height: auto;
  height: initial;
  border-radius: 5px;
  width: 120px;
  padding: 5px;
  margin: 0 auto;
  display: block;
  right: auto;
  right: initial;
}

#modal{
  min-width: 300px;
  padding: 20px;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 0 5px rgba(0,0,0,.2);
  -webkit-transform: translate(-250%,-50%) scale(0);
          transform: translate(-250%,-50%) scale(0);

  -webkit-transition: -webkit-transform .3s ease;

  transition: -webkit-transform .3s ease;

  transition: transform .3s ease;

  transition: transform .3s ease, -webkit-transform .3s ease;
}
#modal.open{
  -webkit-transform: translate(-50%,-50%) scale(1);
          transform: translate(-50%,-50%) scale(1); 
}
#modal h1{
  text-align: center;
  margin-top: 0;
  padding: 0 0 10px 0;
}
#modal input{
  display: block;
  text-align: center;
  width: 100%;
  margin: 20px 0;
}
#modal button {
	position: absolute;
	right: -10px;
	top: -10px;
	background-color: #00a5ff;
	color: white;
	border: 0;
	width: 50px;
	height: 45px;
	line-height: 20px;
	text-align: center;
